import { responsiveArray } from 'antd/lib/_util/responsiveObserve';
import {
    // create, backgroundClient, ChromeMessage
    backgroundClient, ChromeMessage
} from '../chrome';

const listenMethods = {
    mVerifyDomain:"verifyDomain",
    mVerifyContract:"verifyContract",
};
const blockemApiKey = "ZNDS0C4GHF69NH2NV2AUXTIMX1DZBQYJ9W6UTBYAP1M4X1C7MM2F9LXK"
const listenEvents = {contentRequest:"contentRequest"}

export default class Background {
    constructor() { 
        this.init();
    }

    init() {
        this.initMessageClient();
    }
    // 
    initMessageClient() {
        console.log('initMessageClient in background');
        backgroundClient.listen("contentRequest", (res, sendResponse) => {
            console.log('backgroud received message :>>',res);
            if (typeof res.params.method === 'undefined') {
                return;
            }
            switch (res.params.method) {
                case listenMethods.mVerifyDomain:
                    handleVerifyDomainRequest(res.params.params,sendResponse)
                    break;
                case listenMethods.mVerifyContract:
                    handleVerifyContractRequest(res.params.params,sendResponse)
                    break;
            }
            //sendResponse(new ChromeMessage("backgroudResponse", {method:res.params.method,data:response}));
        });
    }
    
}


function handleVerifyDomainRequest(parmas,sendResponse){
    if (typeof parmas === 'undefined' || typeof parmas.domain === 'undefined' || parmas.domain === ""){
       return sendResponse(new ChromeMessage("backgroudResponse", {method:listenMethods.mVerifyDomain,data:defaultVerifyDomainResult}));
    }
    return apiVerifyDomain(parmas.domain,sendResponse)
}
function handleVerifyContractRequest(parmas,sendResponse){
    if (typeof parmas === 'undefined' || typeof parmas.contractAddress === 'undefined' || parmas.contractAddress === ""){
       return sendResponse(new ChromeMessage("backgroudResponse", {method:listenMethods.mVerifyContract,data:defaultVerifyContractResult}));
    }
    return apiVerifyContract(parmas.contractAddress,sendResponse)
}

const defaultVerifyDomainResult = {
    code: 0,
    data: {
        domain_name: "",
        type_string: 'white',
        origin: '',
    }
}
const defaultVerifyContractResult = {
   address:"",
   trust_percentage:100,
   is_project_verified:false,
}

function apiVerifyDomain(domain,sendResponse){

    const baseDomainURL = 'https://api.test.mises.site/api/v1';
    const fetchUrl = `${baseDomainURL}/phishing_site/check?domain_name=${domain}`;
    return new Promise((resolve, reject) => {
         // 
    const timer = setTimeout(() => {
        sendResponse(new ChromeMessage("backgroudResponse", {method:listenMethods.mVerifyDomain,data:defaultVerifyDomainResult}));
    }, 3000);
    fetch(fetchUrl)
        .then((fetchRes) => {
            console.log(fetchRes);
            clearTimeout(timer);
            return fetchRes.json();
        })
        .then((data) => {
            sendResponse(new ChromeMessage("backgroudResponse", {method:listenMethods.mVerifyDomain,data:data}));
        })
        .catch((err) => {sendResponse(new ChromeMessage("backgroudResponse", {method:listenMethods.mVerifyDomain,data:defaultVerifyDomainResult}));});
    });
   
}

function apiVerifyContract(contractAddress,sendResponse){

    const baseURL = 'https://api.blockem.io';
    const fetchUrl = `${baseURL}/trust/${contractAddress}`;
    const method = listenMethods.mVerifyContract;
    return new Promise((resolve, reject) => {
         // 
    const timer = setTimeout(() => {
        sendResponse(new ChromeMessage("backgroudResponse", {method: method, data: defaultVerifyContractResult}));
    }, 3000);
    fetch(fetchUrl,
        {
            method: 'GET',
            headers:{"Authorization":"Bearer " + blockemApiKey}
        })
        .then((fetchRes) => {
            console.log("apiVerifyContract: ",fetchRes);
            clearTimeout(timer);
            return fetchRes.json();
        })
        .then((data) => {
            let response = data
            console.log("apiVerifyContract: ",data);
            console.log("apiVerifyContract: ",response);
            if (typeof response.trust_percentage === 'undefined') {
                response = defaultVerifyContractResult;
            };
            sendResponse(new ChromeMessage("backgroudResponse", {method: method, data: data}));
        })
        .catch((err) => {sendResponse(new ChromeMessage("backgroudResponse", {method: method, data: defaultVerifyContractResult}));});
    });
   
}




